import { height, width } from '@mui/system'
import React from 'react'
import { useEffect, useState } from 'react';
import { Link, useNavigate, useParams } from 'react-router-dom'
// import payBg from '../../assets/images/payBg.jpg'
import payBg from '../../AdvertisementModule/images/payBg.jpg'
import AdvertisementApiList from '../AdvertisementApiList';
import axios from 'axios';
import ApiHeaderAdvrt from '../ApiHeaderAdvrt';
import AxiosInterceptors from '../../../Components/GlobalData/AxiosInterceptors';


function PaymentScreen() {
    const { paymentid, wrkflowId } = useParams();
    const { getPaymentsuccessDetails } = AdvertisementApiList();

    console.log("id..in payment screen", paymentid)
    console.log("workflow id..in payment screen", wrkflowId)
    const navigate = useNavigate();

    const [paymentDetails, setpaymentDetails] = useState()

    ///////////{*** GETTING ULB DATA***}/////////
    useEffect(() => {
        getPaymentSuccessData()
    }, [])
    const getPaymentSuccessData = () => {
        const requestBody = {
            paymentId: paymentid,
            workflowId: wrkflowId,
        }
        AxiosInterceptors.post(`${getPaymentsuccessDetails}`, requestBody, ApiHeaderAdvrt())
            .then(function (response) {
                console.log('payment data', response)
                setpaymentDetails(response.data.data)
            })
            .catch(function (error) {
                console.log('errorrr.... ', error);
            })
    }
    console.log("payment details", paymentDetails)
    return (
        <>

            {/* <div className='grid grid-cols-1 md:grid-cols-1 lg:grid-cols-1 bg-green-100 border p-8 h-screen w-full '  style={{backgroundImage: `url(${payBg})`,backgroundBlendMode:'darken',backgroundRepeat:'no-repeat',backgroundSize:'cover',}}> */}
            <div className='grid grid-cols-1 md:grid-cols-1 lg:grid-cols-1  border p-8 h-screen w-full  mt-24' >
                {/* <h1 className='text-3xl font-semibold text-center'>PAYMENT SUCCESSFUL</h1> */}
                <div className='grid grid-cols-1 md:grid-cols-1 lg:grid-cols-1 bg-gray-50 w-[32rem] h-[32rem] container mx-auto  shadow-2xl z-50 opacity-100'>
                    <div className='grid grid-cols-1 md:grid-cols-1 lg:grid-cols-1'>
                        {/* <img src='https://cdn-icons-png.flaticon.com/512/2169/2169862.png' className='h-36 mx-auto' /> */}
                    </div>
                    <div className='grid grid-cols-1 md:grid-cols-1 lg:grid-cols-1'>
                        <img src='https://cdn-icons-png.flaticon.com/512/5610/5610944.png' className='h-20 mx-auto opacity-75' />
                    </div>
                    <div className='grid grid-cols-1 md:grid-cols-1 lg:grid-cols-1 '>
                        <h1 className='text-center text-2xl  text-green-500'>Payment Successful</h1>
                    </div>

                    <div className='grid grid-cols-2 md:grid-cols-1 lg:grid-cols-1 p-4 pt-8'>
                        <div className='flex px-4'>
                            <div className='flex-1'>
                                <h1 className='text-gray-500 font-semibold text-md'>Payment Mode</h1>
                            </div>
                            <div className='flex-1'>
                                <h1 className='float-right text-gray-500 font-semibold text-md'>{paymentDetails?.payment_details?.paymentMode}</h1>
                            </div>
                        </div>
                        <div className='flex px-4 pt-4'>
                            <div className='flex-1'>
                                <h1 className='text-gray-600 font-bold text-md'>Amount Paid</h1>
                            </div>
                            <div className='flex-1'>
                                <h1 className='float-right text-gray-600 font-bold text-md'>{paymentDetails?.payment_details?.amount}</h1>
                            </div>
                        </div>
                        <div className='flex px-4 pt-4'>
                            <div className='flex-1'>
                                <h1 className='text-gray-500 font-semibold text-md'>Transcation No</h1>
                            </div>
                            <div className='flex-1'>
                                <h1 className='float-right text-gray-500 font-semibold text-md'>{paymentDetails?.payment_details?.transactionNo}</h1>
                            </div>
                        </div>

                    </div>

                    <div className='grid grid-cols-1 md:grid-cols-1 lg:grid-cols-1 mb-4'>
                        <div className=''>
                            {/* <div className='text-center'>
                                <Link to='/advertisement-dashboard'>
                                    <button className='text-sm bg-indigo-500 text-white px-4 py-2 text-center'>
                                        CLOSE
                                    </button>
                                </Link>
                            </div> */}
                            <div className='text-center'>
                                <button className='text-md bg-indigo-500 text-white px-4 py-2 text-center' onClick={()=>navigate(`/advtPaymentReceipt/${paymentid}/${wrkflowId}`)}>
                                    View Receipt
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>



        </>
    )
}

export default PaymentScreen